h1{
    color: dodgerblue;
    text-decoration: solid;
}

table{
    margin-left:auto;
    margin-right:auto;
}

h2 {
    background-color: dodgerblue;
    color: white;
    padding-top: 1rem;
    padding-bottom: 1rem;
   }

.todologo{
    height: 50px;
    weight: 50px;
}

.btn{
    background-color: dodgerblue;
    color: white;
    font-size: 14px;
    padding: 10px 24px;
    margin-bottom: 10px;
    margin-right: 4px; 
    border: none; 
    border-radius: 4px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  
}

.btn:hover{
    background-color: deepskyblue;
}

.btn:active{
    background-color:turquoise;
}

input{

    padding: 10px 24px;
    margin-bottom: 10px;
    margin-right: 4px; 
    border-radius: 4px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

.text-danger{
    color: red;
    font-weight: 500;
}